import type { Job } from '../gameplay/JobManager';
import { getRandomRival } from '../data/RivalCrews';

function formatSats(n: number): string {
  if (n >= 1_000_000) return `${(n / 1_000_000).toFixed(2)}M sats`;
  if (n >= 1_000)     return `${(n / 1_000).toFixed(0)}K sats`;
  return `${n} sats`;
}

const ZONE_LABELS: Record<string, string> = {
  cbd: ' CBD',
  footscray: ' Footscray',
  brunswick: ' Brunswick',
  richmond: ' Richmond',
  stkilda: ' St Kilda',
};

const TYPE_LABELS: Record<string, string> = {
  residential: ' Residential',
  commercial:  ' Commercial',
  heritage:    ' Heritage',
  emergency:   ' Emergency',
};

export class JobBoard {
  private overlay: HTMLDivElement;
  private visible = false;
  private jobs: Job[] = [];

  constructor(private onAccept: (job: Job) => void) {
    this.overlay = document.createElement('div');
    this.overlay.style.cssText = `
      position: fixed;
      inset: 0;
      background: rgba(10, 10, 20, 0.88);
      z-index: 2000;
      display: none;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      overflow-y: auto;
      padding: 16px 12px 32px;
      font-family: system-ui, -apple-system, sans-serif;
      box-sizing: border-box;
    `;

    // Inject styles once
    if (!document.getElementById('job-board-styles')) {
      const style = document.createElement('style');
      style.id = 'job-board-styles';
      style.textContent = `
        .jb-card {
          background: rgba(255,255,255,0.06);
          border: 1px solid rgba(255,255,255,0.12);
          border-radius: 12px;
          padding: 16px;
          width: 100%;
          max-width: 480px;
          margin-bottom: 12px;
          box-sizing: border-box;
          transition: background 0.15s;
        }
        .jb-card:hover {
          background: rgba(255,255,255,0.10);
        }
        .jb-card-contested {
          background: linear-gradient(135deg, rgba(40,20,5,0.95), rgba(25,10,0,0.98));
          border: 2px solid #E8A830;
          border-radius: 14px;
          padding: 0;
          width: 100%;
          max-width: 480px;
          margin-bottom: 14px;
          box-sizing: border-box;
          box-shadow: 0 0 24px rgba(232,168,48,0.25), inset 0 0 40px rgba(232,168,48,0.04);
          animation: jbContestPulse 2.5s ease-in-out infinite;
          overflow: hidden;
        }
        @keyframes jbContestPulse {
          0%, 100% { box-shadow: 0 0 18px rgba(232,168,48,0.22), inset 0 0 40px rgba(232,168,48,0.04); }
          50%       { box-shadow: 0 0 36px rgba(232,168,48,0.45), inset 0 0 40px rgba(232,168,48,0.08); }
        }
        .jb-war-banner {
          background: linear-gradient(90deg, #B8840A, #E8A830, #B8840A);
          padding: 6px 16px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          gap: 8px;
        }
        .jb-war-label {
          color: #000;
          font-size: 11px;
          font-weight: 900;
          letter-spacing: 3px;
        }
        .jb-war-rival {
          color: #000;
          font-size: 11px;
          font-weight: 800;
          letter-spacing: 1px;
          opacity: 0.8;
        }
        .jb-card-contested-body {
          padding: 14px 16px 16px;
        }
        .jb-battle-btn {
          background: linear-gradient(135deg, #E8A830, #C87010);
          color: #000;
          border: none;
          border-radius: 10px;
          padding: 13px 24px;
          font-size: 16px;
          font-weight: 900;
          cursor: pointer;
          width: 100%;
          letter-spacing: 1px;
          transition: opacity 0.15s, transform 0.1s;
          min-height: 48px;
          text-transform: uppercase;
        }
        .jb-battle-btn:hover { opacity: 0.88; transform: translateY(-1px); }
        .jb-battle-btn:active { transform: translateY(0); opacity: 1; }
        .jb-card-header {
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          gap: 8px;
          margin-bottom: 4px;
        }
        .jb-title {
          color: #fff;
          font-size: 17px;
          font-weight: 700;
          line-height: 1.2;
        }
        .jb-pay {
          color: #5EDB7D;
          font-size: 18px;
          font-weight: 800;
          white-space: nowrap;
        }
        .jb-meta {
          display: flex;
          gap: 8px;
          flex-wrap: wrap;
          margin-bottom: 6px;
        }
        .jb-badge {
          color: #aaa;
          font-size: 12px;
          font-weight: 600;
          background: rgba(255,255,255,0.08);
          border-radius: 6px;
          padding: 2px 8px;
        }
        .jb-emergency-badge {
          color: #ff6b6b;
          background: rgba(255,80,80,0.14);
        }
        .jb-client {
          color: #b0c4e8;
          font-size: 13px;
          font-style: italic;
          margin-bottom: 6px;
        }
        .jb-desc {
          color: #ccc;
          font-size: 14px;
          line-height: 1.5;
          margin-bottom: 12px;
        }
        .jb-accept-btn {
          background: #C1666B;
          color: #fff;
          border: none;
          border-radius: 10px;
          padding: 12px 24px;
          font-size: 16px;
          font-weight: 700;
          cursor: pointer;
          width: 100%;
          letter-spacing: 0.5px;
          transition: background 0.15s, transform 0.1s;
          min-height: 48px;
        }
        .jb-accept-btn:hover {
          background: #d4787d;
          transform: translateY(-1px);
        }
        .jb-accept-btn:active {
          transform: translateY(0);
          background: #a54f54;
        }
        .jb-close-btn {
          position: sticky;
          top: 0;
          align-self: flex-end;
          background: rgba(255,255,255,0.12);
          color: #fff;
          border: none;
          border-radius: 50%;
          width: 44px;
          height: 44px;
          font-size: 22px;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-shrink: 0;
          z-index: 10;
          transition: background 0.15s;
        }
        .jb-close-btn:hover {
          background: rgba(255,255,255,0.22);
        }
        .jb-no-jobs {
          color: #888;
          font-size: 18px;
          text-align: center;
          margin-top: 40px;
        }
      `;
      document.head.appendChild(style);
    }

    document.body.appendChild(this.overlay);
  }

  show(jobs: Job[]): void {
    this.jobs = jobs.slice(0, 8);
    this.visible = true;
    this._render();
    this.overlay.style.display = 'flex';
  }

  hide(): void {
    this.visible = false;
    this.overlay.style.display = 'none';
  }

  isVisible(): boolean {
    return this.visible;
  }

  //  Contested (battle) card  all inline styles, no class dependencies 
  private _makeContestedCard(job: Job): HTMLElement {
    const rival = getRandomRival();
    const stars = ''.repeat(Math.round(rival.difficulty * 5)) + ''.repeat(5 - Math.round(rival.difficulty * 5));
    const cleanTitle = job.title.replace(/^\s*/, '');

    const card = document.createElement('div');
    card.style.cssText = `
      width:100%; max-width:480px; box-sizing:border-box;
      background:linear-gradient(160deg,#1a0e00,#120900);
      border:2px solid #E8A830; border-radius:16px;
      margin-bottom:16px; overflow:visible;
      box-shadow:0 0 20px rgba(232,168,48,0.2);
      font-family:system-ui,sans-serif;
    `;

    card.innerHTML = `
      <!-- Gold banner -->
      <div style="
        background:linear-gradient(90deg,#B8840A,#E8A830,#B8840A);
        border-radius:13px 13px 0 0;
        padding:8px 14px;
        display:flex; align-items:center; justify-content:space-between;
      ">
        <span style="color:#000;font-size:12px;font-weight:900;
          letter-spacing:2px;font-family:'Arial Black',Arial,sans-serif;">
           CONTRACT WAR
        </span>
        <span style="color:#000;font-size:11px;font-weight:800;">vs ${rival.name}</span>
      </div>

      <!-- Body -->
      <div style="padding:14px 16px 16px;">

        <!-- Title + pay row -->
        <div style="display:flex;justify-content:space-between;
          align-items:flex-start;gap:8px;margin-bottom:10px;">
          <div style="color:#FFD880;font-size:16px;font-weight:900;
            line-height:1.25;flex:1;">${cleanTitle}</div>
          <div style="color:#E8A830;font-size:17px;font-weight:900;
            white-space:nowrap;"> ${formatSats(job.pay)}</div>
        </div>

        <!-- Rival + difficulty strip -->
        <div style="
          display:flex;align-items:center;gap:10px;
          background:rgba(232,168,48,0.08);
          border:1px solid rgba(232,168,48,0.18);
          border-radius:8px;padding:8px 12px;margin-bottom:10px;
        ">
          <div style="font-size:20px;flex-shrink:0;"></div>
          <div style="flex:1;">
            <div style="color:#E8A830;font-size:10px;font-weight:900;letter-spacing:1px;">
              RIVAL CREW
            </div>
            <div style="color:#fff;font-size:13px;font-weight:800;">${rival.name}</div>
            <div style="color:#aaa;font-size:10px;">${stars}</div>
          </div>
          <div style="text-align:right;">
            <div style="color:#2ECC40;font-size:10px;font-weight:700;">FULL PAY</div>
            <div style="color:#2ECC40;font-size:13px;font-weight:900;">WIN = 100%</div>
          </div>
        </div>

        <!-- Zone + type badges -->
        <div style="display:flex;gap:6px;flex-wrap:wrap;margin-bottom:10px;">
          <span style="
            color:#aaa;font-size:11px;font-weight:600;
            background:rgba(255,255,255,0.08);
            border-radius:6px;padding:3px 8px;
          ">${ZONE_LABELS[job.zone] ?? job.zone}</span>
          <span style="
            color:${job.type === 'emergency' ? '#ff6b6b' : '#aaa'};
            font-size:11px;font-weight:600;
            background:${job.type === 'emergency' ? 'rgba(255,80,80,0.14)' : 'rgba(255,255,255,0.08)'};
            border-radius:6px;padding:3px 8px;
          ">${TYPE_LABELS[job.type] ?? job.type}</span>
          <span style="color:#888;font-size:11px;font-weight:600;
            background:rgba(255,255,255,0.06);
            border-radius:6px;padding:3px 8px;">
             ${job.client}
          </span>
        </div>

        <!-- Description -->
        <div style="
          color:#bbb;font-size:13px;line-height:1.5;
          margin-bottom:14px;
        ">${job.description}</div>

        <!-- Battle button -->
        <button id="battle-btn-${job.id}" style="
          width:100%;
          background:linear-gradient(135deg,#E8A830,#C07010);
          color:#000;border:none;border-radius:12px;
          padding:15px 24px;font-size:16px;font-weight:900;
          letter-spacing:1px;cursor:pointer;
          font-family:'Arial Black',Arial,sans-serif;
          min-height:52px;touch-action:manipulation;
          box-shadow:0 4px 16px rgba(232,168,48,0.3);
        "> BATTLE FOR THIS JOB</button>
      </div>
    `;

    card.querySelector(`#battle-btn-${job.id}`)
      ?.addEventListener('click', () => this.onAccept(job));

    return card;
  }

  //  Regular job card 
  private _makeRegularCard(job: Job): HTMLElement {
    const card = document.createElement('div');
    card.className = 'jb-card';

    const cardHeader = document.createElement('div');
    cardHeader.className = 'jb-card-header';

    const jobTitle = document.createElement('div');
    jobTitle.className = 'jb-title';
    jobTitle.textContent = job.title;
    cardHeader.appendChild(jobTitle);

    const pay = document.createElement('div');
    pay.className = 'jb-pay';
    pay.textContent = ` ${formatSats(job.pay)}`;
    cardHeader.appendChild(pay);

    card.appendChild(cardHeader);

    const meta = document.createElement('div');
    meta.className = 'jb-meta';

    const zoneBadge = document.createElement('span');
    zoneBadge.className = 'jb-badge';
    zoneBadge.textContent = ZONE_LABELS[job.zone] ?? job.zone;
    meta.appendChild(zoneBadge);

    const typeBadge = document.createElement('span');
    typeBadge.className = `jb-badge${job.type === 'emergency' ? ' jb-emergency-badge' : ''}`;
    typeBadge.textContent = TYPE_LABELS[job.type] ?? job.type;
    meta.appendChild(typeBadge);

    if (job.timeLimit > 0) {
      const timeBadge = document.createElement('span');
      timeBadge.className = 'jb-badge jb-emergency-badge';
      timeBadge.textContent = ` ${Math.round(job.timeLimit / 60)} min limit`;
      meta.appendChild(timeBadge);
    }

    card.appendChild(meta);

    const clientEl = document.createElement('div');
    clientEl.className = 'jb-client';
    clientEl.textContent = `Client: ${job.client}`;
    card.appendChild(clientEl);

    const desc = document.createElement('div');
    desc.className = 'jb-desc';
    desc.textContent = job.description;
    card.appendChild(desc);

    const acceptBtn = document.createElement('button');
    acceptBtn.className = 'jb-accept-btn';
    acceptBtn.textContent = 'ACCEPT ';
    acceptBtn.addEventListener('click', () => this.onAccept(job));
    card.appendChild(acceptBtn);

    return card;
  }

  private _render(): void {
    this.overlay.innerHTML = '';

    //  Header row 
    const header = document.createElement('div');
    header.style.cssText = `
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      max-width: 480px;
      margin-bottom: 16px;
      position: sticky;
      top: 0;
      padding: 8px 0;
      background: rgba(10,10,20,0.92);
      z-index: 5;
    `;

    const title = document.createElement('div');
    title.textContent = ' JOBS AVAILABLE';
    title.style.cssText = `
      color: #fff;
      font-size: 20px;
      font-weight: 900;
      letter-spacing: 1px;
    `;
    header.appendChild(title);

    const closeBtn = document.createElement('button');
    closeBtn.className = 'jb-close-btn';
    closeBtn.textContent = '';
    closeBtn.setAttribute('aria-label', 'Close');
    closeBtn.addEventListener('click', () => this.hide());
    header.appendChild(closeBtn);

    this.overlay.appendChild(header);

    //  Job cards 
    if (this.jobs.length === 0) {
      const empty = document.createElement('div');
      empty.className = 'jb-no-jobs';
      empty.textContent = "You've smashed all the jobs, legend. ";
      this.overlay.appendChild(empty);
      return;
    }

    const contested = this.jobs.filter(j => j.isContested);
    const regular   = this.jobs.filter(j => !j.isContested);

    //  CONTRACT WARS section 
    if (contested.length > 0) {
      const cwHead = this._sectionHeading(
        ' CONTRACT WARS',
        'linear-gradient(90deg,#E8A830,#FF6600)',
        'rgba(232,168,48,0.12)',
        'rgba(232,168,48,0.3)',
      );
      const cwSub = document.createElement('div');
      cwSub.style.cssText = `
        color:#ccc; font-size:11px; font-family:system-ui,sans-serif;
        margin:-10px 0 12px; width:100%; max-width:480px; text-align:center;
      `;
      cwSub.textContent = 'Beat the rival crew  Scaffold + Tower Defence  fastest time wins';
      this.overlay.appendChild(cwHead);
      this.overlay.appendChild(cwSub);
      for (const job of contested) this.overlay.appendChild(this._makeContestedCard(job));
    }

    //  REGULAR JOBS section 
    if (regular.length > 0) {
      const regHead = this._sectionHeading(
        ' AVAILABLE JOBS',
        'linear-gradient(90deg,#5EDB7D,#3BAA5A)',
        'rgba(94,219,125,0.08)',
        'rgba(94,219,125,0.2)',
      );
      this.overlay.appendChild(regHead);
      for (const job of regular) this.overlay.appendChild(this._makeRegularCard(job));
    }
  }

  private _sectionHeading(
    label: string,
    gradient: string,
    bgColor: string,
    borderColor: string,
  ): HTMLElement {
    const wrap = document.createElement('div');
    wrap.style.cssText = `
      width:100%; max-width:480px;
      display:flex; align-items:center; gap:10px;
      margin-bottom:12px; margin-top:4px;
      background:${bgColor};
      border:1px solid ${borderColor};
      border-radius:10px; padding:8px 14px;
      box-sizing:border-box;
    `;
    wrap.innerHTML = `
      <div style="
        font-size:clamp(12px,3.5vw,15px); font-weight:900; letter-spacing:2px;
        background:${gradient};
        -webkit-background-clip:text; -webkit-text-fill-color:transparent;
        background-clip:text; white-space:nowrap;
      ">${label}</div>
      <div style="flex:1;height:1px;background:${borderColor};opacity:0.5;"></div>
    `;
    return wrap;
  }
}
